.dp-info-icon {
	width: 15px;
	height: 15px;
	//display: none;
	margin-left: 5px;
	margin-bottom: 5px;
	flex-shrink: 0;
	opacity: 0.8;
	cursor: help;
	transition: opacity 0.3s ease-in-out;
	pointer-events: all;

	&--inline {
		display: inline-block;
		vertical-align: middle;
		margin-bottom: 0px;
	}

	&--bigger {
		width: 17px;
		height: 17px;
	}

	&--pre {
		margin-left: 0px;
		margin-right: 5px;
		margin-bottom: 3px;
	}

	&:hover {
		opacity: 1;
	}

	svg {
		path {
			transition: fill 0.3s ease-in-out;
			fill: rgba(var(--dpu-color-2), 1);
		}
	}

	&--pro-only {
		svg {
			path {
				opacity: 0.5;
			}
		}
	}

	&--in-table-header {
		position: absolute;
		top: 7px;
		right: 10px;
	}
}