.tippy-box {
	pointer-events: all;
	opacity: 1 !important;
	box-shadow: 0 0px 10px 0 rgba(var(--dpu-color-2), 0.03) !important;

	.tippy-content {
		padding: 15px 20px 20px;
		opacity: 1 !important;

		.info-tip-title {
			display: block;
			text-transform: uppercase;
			font-size: 15px;
			padding-bottom: 5px;
		}

		br {
			display: block;
			margin-bottom: 8px !important;
			content: " ";
		}

		a {
			color: rgba(var(--dpu-color-2-text), 1) !important;
			font-size: 16px;
			font-weight: 600;
		}
	}


	&[data-theme="material"] {
		font-weight: 400 !important;
		background-color: rgba(var(--dpu-color-2), 1) !important;
		color: rgba(var(--dpu-color-2-text), 1) !important;
	}

	&[data-placement="top"] {
		&[data-theme="material"] {
			.tippy-arrow {
				&::before {
					border-top-color: rgba(var(--dpu-color-2), 1) !important;
				}
			}
		}
	}

	&[data-placement="bottom"] {
		&[data-theme="material"] {
			.tippy-arrow {
				&::before {
					border-bottom-color: rgba(var(--dpu-color-2), 1) !important;
				}
			}
		}

	}
}