.dpit-g-options {
	h1 {
		text-transform: uppercase;

		strong {
			font-size: 1.1em;
			text-shadow: 0px 0px 1px rgba(var(--dpu-color-2), 0.9);
			display: block;
			color: rgb(var(--dpu-color-3)) !important;
			margin-bottom: 0.2em;
		}
	}

	.nav-tab-wrapper {
		display: inline-block;
		padding-right: 10px;
		padding-left: 10px;
		padding-top: 0;

		a {
			margin-top: 10px;

			&:first-child {
				margin-left: 0;
			}

			&.nav-tab-active {
				background-color: rgb(var(--dpu-color-3));
				color: rgb(var(--dpu-color-3-text));
			}
		}
	}

	form {
		padding: 0 10px;
	}

	&__desc {
		margin-top: 0;
		font-size: 1.1em;

		&--main {
			margin-top: 0.8em;
			margin-bottom: 0;
			max-width: unset;
		}
	}

	&__width-fix {
		max-width: 60%;

		@include atS {
			max-width: 490px;
		}
	}

	&__heading-note {
		vertical-align: super;
		transform: translateY(-30%);
		font-size: 0.6rem;
		font-weight: 500;
		letter-spacing: 1px;
		color: rgb(var(--dpu-color-2)) !important;
		background-color: rgb(var(--dpu-color-2-text)) !important;
		border: 1px solid rgba(var(--dpu-color-2), 0.2) !important;
		display: inline-block;
		line-height: 1.7 !important;
		padding: 2px 8px;
		margin: 5px 0;
		text-transform: uppercase;
		text-decoration: none;
		border-radius: 2px;
		@include transitionFast(border-color);
		box-shadow: none !important;

		&:hover {
			border-color: rgba(var(--dpu-color-2), 0.4) !important;
		}
	}
}